.back-btn {
  height: 50px;
  text-align: center;
  max-width: 130px;
  cursor: pointer;
  color: #000;
  margin-top: 20px;
  font-family: Amatic SC, cursive;
  font-size: xx-large;
  font-weight: 900;
  line-height: 50px;
  transition: all .3s;
  position: relative;
}

.back-btn span {
  transition: all .3s;
}

.back-btn:before {
  content: "";
  width: 100%;
  height: 100%;
  z-index: 10;
  opacity: 0;
  border-top: 1px solid rgba(255, 255, 255, .5);
  border-bottom: 1px solid rgba(255, 255, 255, .5);
  transition: all .3s;
  position: absolute;
  bottom: 0;
  left: 0;
  transform: scale(.1, 1);
}

.back-btn:hover span {
  letter-spacing: 2px;
}

.back-btn:hover:before {
  opacity: 1;
  transform: scale(1);
}

.back-btn:after {
  content: "";
  width: 100%;
  height: 100%;
  z-index: 1;
  background: rgba(239, 230, 230, .05);
  border: 1px solid rgba(255, 255, 255, .18);
  border-radius: 10px;
  transition: all .3s;
  position: absolute;
  bottom: 0;
  left: 0;
  box-shadow: 0 8px 32px rgba(31, 38, 135, .37);
}

.back-btn:hover:after {
  opacity: 0;
  transform: scale(.1, 1);
}

